/* eslint-disable curly */
/* eslint-disable @typescript-eslint/naming-convention */
import { stringify as queryStringify } from "querystring";
import { request as httpRequest, get as getHttp } from "http";
import { request as httpsRequest, get as getHttps } from "https";
import { URL } from "url";
import { readFileSync } from 'fs';
import { extname, basename, parse } from "path";
import { mimeType } from "./Util/Mime";


export default class Request {

    private protocol: string;
    private host: string;
    private port: string;
    private path: string;
    private httpRequest;
    private httpRequestService;
    private getHttp;
    private allow: any;

    public lastError: string = '';

    constructor(private readonly api: string, private readonly token: string,
        private apiService: string) {
        const url = new URL(this.api);
        this.host = url.hostname;
        this.protocol = url.protocol;
        this.port = this.protocol.indexOf('https') > -1 ? '443' : (url.port ? url.port : '80');
        this.path = url.pathname;
        this.allow = null;
        this.httpRequest = this.protocol.indexOf('https') > -1 ? httpsRequest : httpRequest;
        this.httpRequestService = this.protocol.indexOf('https') > -1 ? httpsRequest : httpRequest;
        this.getHttp = this.protocol.indexOf('https') > -1 ? getHttps : getHttp;
        console.log('protocol: ', this.protocol);
        console.log('port:', this.port);
    }

    public getTypeContent(type: string) {
        switch (type) {
            case 'json': return 'application/json; charset=UTF-8';
            case 'x-form': return 'application/x-www-form-urlencoded; charset=UTF-8';
            case 'form': return 'application/form-data; charset=UTF-8';
        }
        return type;
    }

    public setAllow(data: any = null) {
        this.allow = data;
    }

    public getHeaders(ty: string = 'json', data?: any): any {
        const header: any = {
            'PRIVATE-TOKEN': this.allow ? this.allow : this.token,
            'Content-Type': this.getTypeContent(ty)
        };
        if (data) header['Content-Length'] = Buffer.byteLength(data);
        return header;
    }

    public getOptions(url: string, method: string, ty: string = 'json'): any {
        console.log('URL:::: ', this.path.substring(1) + url);
        url = this.path.substring(1) + url;
        return {
            host: this.host,
            port: this.port,
            path: url,
            method: method,
            headers: this.getHeaders(ty, 0)
        };
    }

    public getOptionsService(url: string, method: string, ty: string = 'json') {
        const uri = new URL(this.apiService);
        this.httpRequestService = uri.protocol.indexOf('https') > -1 ? httpsRequest : httpRequest;
        const port = uri.protocol.indexOf('https') > -1 ? '443' : (uri.port ? uri.port : '80');
        return {
            host: uri.hostname,
            port: port,
            path: uri.pathname + url,
            method: method,
            headers: this.getHeaders(ty, 0)
        };
    }

    /**
     * Servicio que conecta con gitlab de forma directa usuario interno
     * @param url 
     * @param params 
     * @param method 
     * @returns 
     */
    private send(url: string, params?: any, method: string = 'GET'): Promise<any> {
        return new Promise<any>((resolve, reject) => {
            let options = this.getOptions(url, method);
            let data: string = '';
            console.log('header options: ', options);
            if (params) {
                data = queryStringify(params);
                options.method = method === 'GET' ? 'POST' : method;
                options.headers = this.getHeaders('x-form', data);
            }
            let req = this.httpRequest(options, (res: any) => {
                res.setEncoding('utf8');
                if (res.statusCode >= 200 && res.statusCode <= 299) {
                    let content: string = '';
                    res.on('data', (dd: string) => {
                        content += dd;
                    });
                    res.on('end', () => {
                        resolve(content);
                    });
                } else {
                    this.lastError = 'Can\'t find the api: ' + this.api;
                    reject('err:' + res.statusCode);
                }
            });
            req.on('error', (err: any) => {
                this.lastError = 'Can\'t connect to api host:' + this.api;
                reject(err);
            });
            data && req.write(data);
            req.end();
        });
    }

    /**
     * servicio para realizar tareas de administrador 
     * @param url 
     * @param data 
     * @param files 
     * @returns 
     */
    async service(url: string, data: any, files?: any, method: string = 'POST', ty: boolean = false): Promise<any> {
        return new Promise<any>((resolve, reject) => {
            const bou = 'zE9Ecx45f41KqWGUxz4b';
            let cc: any = [];
            if (data) for (const key in data) {
                cc.push(this.addBoundary(bou, 'D', key, data[key]));
            }
            if (files) for (const key in files) {
                cc.push(this.addBoundary(bou, 'F', key, files[key]));
                cc.push(this.addBoundaryBinary(files[key]));
                cc.push(Buffer.from("\r\n", "utf8"));
            }
            cc.push(this.endBoundary(bou));
            const content = Buffer.concat(cc);
            const contentType = "multipart/form-data; boundary=" + bou;
            const options = ty
                ? this.getOptions(url, method, contentType)
                : this.getOptionsService(url, method, contentType);
            let dd = '';
            const req = this.httpRequestService(options, response => {
                response.on('data', (chunk: any) => dd += chunk);
                response.on('end', () => {
                    try {
                        resolve(dd ? JSON.parse(dd):dd);
                    } catch (err: any) {
                        console.log('[(' + dd + ')]', err);
                        reject(err.message);
                    }
                });
            });
            req.on('error', (err) => {
                reject(err.message);
                console.error(`problem with request: ${err.message}`);
            });
            req.write(content);
            req.end();
        });
    }


    public async sendAllow(url: string, data: any, files?: any, method: string = 'POST', isAdmin:boolean=false): Promise<any> {
        if (this.apiService) {
            let dd: any = {};
            let rs: any = {};
            try {
                rs = await this.service('/allow', { id: 0 });
            } catch (err) {
                dd.success = false;
                dd.message = err;
            }
            if (rs && rs.success && rs.data) {
                this.setAllow(rs.data);
                try {
                    dd = await this.service(url, data, files, method, true);
                } catch (err) {
                    dd.success = false;
                    dd.message = err;
                }
                this.setAllow(null);
            }
            return dd;
        } else { //Privilegios de administrador
            const _api = this.apiService + '';
            this.apiService = this.api + '';
            let dd: any = {};
            try {
                dd = await this.service(url, data, files, method, true);
            } catch (err) {
                dd.success = false;
                dd.message = err;
            }
            this.apiService = _api + '';
            return dd;
        }
    }

    /**
     * Agregar un elemento del formulario
     * @param boundary 
     * @param type 
     * @param name 
     * @param value 
     * @returns 
     */
    public addBoundary(boundary: string, type: string, name: string, value: string) {
        if (type === 'D') {
            return Buffer.from("--" + boundary + "\r\n" +
                "Content-Disposition: form-data; name=\"" + name + "\";\r\n\r\n" +
                value + "\r\n", 'utf8');
        }
        else if (type === 'F') {
            const mime = mimeType(extname(value));
            return Buffer.from("--" + boundary + "\r\n" +
                "Content-Disposition: form-data; name=\"" + name + "\"; " +
                "filename=\"" + basename(value) + "\"\r\n" +
                "Content-Type: " + mime + "\r\n" +
                "Content-Type: application/octet-stream\r\n\r\n", 'utf8');
        }
    }

    public addBoundaryBinary($file: string) {
        const content: any = readFileSync($file);
        return Buffer.from(content, 'binary');
    }

    public endBoundary(boundary: string): any {
        return Buffer.from("--" + boundary + "--\r\n", 'utf8');
    }


    async getJson(action: string, params: any = null, method: string = 'GET'): Promise<any> {
        let data = '';
        try {
            data = await this.send(action, params, method);
            return JSON.parse(data);
        } catch (err: any) {
            console.warn(err, ':::', data);
            return { message: err + ', ' + this.lastError };
            //return err;
        }
    }

    getB64(url: string): Promise<any> {
        return new Promise<any>((resolve, reject) => {
            this.getHttp(url, (resp) => {
                resp.setEncoding('base64');
                let body = "";
                resp.on('data', (data) => { body += data });
                resp.on('end', () => {
                    resolve({ data: body, contentType: resp.headers["content-type"] });
                });
            }).on('error', (e) => {
                reject(`error: ${e.message}`);
            });
        });
    }

    async getImgB64(url: string): Promise<any> {
        const rd = await this.getB64(url);
        return "data:" + rd.contentType + ";base64," + rd.data;
    }

}