/* eslint-disable curly */

 export function getListOptions(data: any): any {
    let res = [];
    if (data) {
        for (const x in data) {
            if (data[x].state === undefined || data[x].state === "active") res.push({
                id: data[x].id,
                value: data[x].name || data[x].title
            });
        }
    }
    return res;
}

export function getList(data: any, format: string = 'options') {
    return getListOptions(data);
}