/* eslint-disable curly */
import Core from './Core';
import { getList } from './Util/Index';

export default class Projects {

    public static me?: Projects;

    constructor(private core: Core) { }

    public static self(core?: Core, cmd?: [string], req?: any) {
        if (!Projects.me) Projects.me = core ? new Projects(core) : undefined;
        if (Projects.me && cmd) Projects.me.router(cmd, req);
        return Projects.me;
    }

    public async router(cmd: [string], req?: any) {
        switch (cmd.shift()) {
            case 'search': return this.search(req);
            //case 'add':  return this.add(req);
        }

    } 
           
    public async search(req: any) {
        const rd = req.content;
        console.log('search: ', rd.key);
        let data = await this.core.req?.getJson('/api/v4/projects?membership=true&search=' + rd.key);
        data = getList(data, req.format);
        console.log('projects data: ', data);
        this.core.postCmd(req.command, data);
    }

}