/* eslint-disable curly */
import Core from './Core';
import { getList } from './Util/Index';

export default class Tasks {

    public static me?: Tasks;

    constructor(private core: Core) { }

    public static self(core?: Core, cmd?: [string], req?: any) {
        if (!Tasks.me) Tasks.me = core ? new Tasks(core) : undefined;
        if (Tasks.me && cmd) Tasks.me.router(cmd, req);
        return Tasks.me;
    }

    public async router(cmd: [string], req?: any) {
        switch (cmd.shift()) {
            //case 'page': return this.core.postCmd('page/tasks');;
            case 'get-statistics': return this.getStatistics(req);
            case 'list': return await this.list(req, 0);
            case 'list-data-0': return await this.list(req, 0);
            case 'list-data-1': return await this.list(req, 1);
            case 'add': return this.add(req);
            case 'del': return this.del(req);
            case 'upd': return this.edit('upd', req);
            case 'state': return this.edit('state', req);
            case 'labels': return this.edit('labels', req);

        }
    }

    // public async page(req?: any) {
    //   //  this.core?.postCmd('page/tasks');
    //   //  this.core.postCmd('page/tasks');
    // }

    //public

    public regData(rd: any) {
        return {
            id: rd.iid,
            idProject: rd.project_id,
            title: rd.title,
            description: rd.description,
            state: rd.state,
            createdAt: rd.created_at,
            updatedAt: rd.updated_at,
            closedAt: rd.closed_at,
            closedBy: rd.closed_by,
            labels: rd.labels,
            assignee: rd.assignee,
            idUser: rd.assignee ? rd.assignee.id : 0,
            author: rd.author,
            dueDate: rd.due_date
        };
    }

    public async list(req: any, ty: number = 0, filter?: string) {
        console.log('filter defined :: ', filter, req);
        let project = { id: 0, name: '' };
        let op = '';
        if (req && req.content && req.content.id) {
            project = { id: req.content.id, name: req.content.name };
            this.core.set('project', project);
        }
        else project = this.core.get('project');
        if (project.id) op = 'projects/' + project.id + '/';
        if (ty === 1) filter = '?author_id=' + this.core.user.id;
        else filter = '?assignee_id=' + this.core.user.id;
        //if (ty === 1) filter = '?author_id=' + this.core.user.id;
        //else filter = '?assignee_id=' + this.core.user.id;

        filter = filter + '&per_page=100';
        console.log('list task - req: ', req);
        const data = await this.core.req?.getJson('/api/v4/' + op + 'issues' + filter);
        console.log('list tasks data: ', data);
        let res = [];
        if (data) {
            for (const x in data) {
                if (ty === 1 && data[x].assignee.id === this.core.user.id) continue;
                res.push(this.regData(data[x]));
            }
        }
        console.log('list task array:::', res);
        return this.core.postCmd(req.command, res);
    }

    public async add(req: any) {
        const rd = req.content;
        const reg = {
            id: rd.idProject,
            assignee_id: rd.idUser,
            title: rd.title,
            description: rd.description,
            due_date: rd.date,
            labels: rd.priority
        };
        console.log('add task: ', reg);
        let data = await this.core.req?.getJson('/api/v4/projects/' + rd.idProject + '/issues', reg);
        if (data) {
            data = this.regData(data);
        }
        console.log('task result data: ', data);
        this.core.postCmd(req.command, data);
    }


    public async edit(ty: string, req: any) {
        const rd = req.content;
        let reg: any = {};
        if (rd.id) reg.issue_iid = rd.id;
        if (rd.idProject) reg.id = rd.idProject;
        if (ty === 'state' || ty === 'upd') {
            if (rd.state) reg.state_event = rd.state === 'closed' ? 'close' : 'reopen';
        }
        if (ty === 'labels' || ty === 'upd') {
            if (rd.labels) reg.labels = rd.labels.join(',');
        }
        if (ty === 'upd') {
            if (rd.idUser) reg.assignee_id = rd.idUser;
            if (rd.title) reg.title = rd.title;
            if (rd.description) reg.description = rd.description;
            if (rd.date) reg.due_date = rd.date;
        }

        console.log('edit task: ', reg);
        let data = await this.core.req?.getJson('/api/v4/projects/' + rd.idProject + '/issues/' + rd.id, reg, 'PUT');
        if (data) {
            data = this.regData(data);
        }
        console.log('task result data: ', data);
        this.core.postCmd(req.command, data);
    }

    public async del(req: any) {
        const rd = req.content;
        console.log('tasl delete:: ',rd);
        const dd = await this.core.req?.sendAllow('/api/v4/projects/' + rd.idProject + '/issues/' + rd.id, {}, {}, 'DELETE');
        const data =  dd && dd.message ? dd: rd;
        console.log('data delete task result: ', dd);
        this.core.postCmd(req.command, data);
    }


    public async getStatistics(req: any, filter?: string) {
        const rd = req.content;
        let sel = { id: 0, name: '' };
        let op = '';
        console.log('::::::', rd);
        if (rd.id) {
            sel = { id: rd.id, name: rd.name };
            op = 'projects/' + rd.id + '/';
        }
        this.core.set('project', sel);
        if (!filter) filter = '?assignee_id=' + this.core.user.id;
        const url = '/api/v4/' + op + 'issues_statistics' + filter + '&labels=';
        console.log(url);
        const baja = await this.core.req?.getJson(url + 'baja');
        const media = await this.core.req?.getJson(url + 'media');
        const alta = await this.core.req?.getJson(url + 'alta');
        const data = {
            baja: this.dataStatistics(baja),
            media: this.dataStatistics(media),
            alta: this.dataStatistics(alta),
        };
        this.core.postCmd(req.command, data);
    }


    public dataStatistics(dd: any) {
        let rs = { opened: 0, closed: 0 };
        if (dd && dd.statistics) rs = {
            opened: dd.statistics.counts.opened,
            closed: dd.statistics.counts.closed
        };
        return rs;
    }


    public setState(req: any) {
        console.log('task:;;;MNM', req);

        // let data = await this.core.req?.getJson('/api/v4/projects/' + rd.idProject + '/issues/' + rd.iid, reg, 'PUT');
        // console.log('task result data: ', data);
        //this.core.postCmd(req.command, data);

        this.core.postCmd(req.command, { id: 1001 });
    }
}