/* eslint-disable curly */
import Core from './Core';
import { getList } from './Util/Index';

export default class Users {

    public static me?: Users;

    constructor(private core: Core) { }

    public static self(core?: Core, cmd?: [string], req?: any) {
        if (!Users.me) Users.me = core ? new Users(core) : undefined;
        if (Users.me && cmd) Users.me.router(cmd, req);
        return Users.me;
    }

    public async router(cmd: [string], req?: any) {
        console.log('user::', cmd, req);
        switch (cmd.shift()) {
            case 'avatar': return this.avatar(req);
            case 'edit': return this.edit(req);
            case 'list-by-project': return this.listByProject(req);
            case 'get-data': return this.getData(req);
            //case 'add':  return this.add(req);
        }
    }

    public getData(req: any) {
        this.core.loadData((user) => {
            this.core.postCmd(req.command, user);
        });
    }

    public async edit(req: any) {
        const rd = req.content;
        let dd: any;
        if (rd.user) {
            rd.user.id = this.core.user.id;
            //dd = await this.core.req?.getJson('/api/v4/users/' + this.core.user.id, rd.user, 'PUT');
            dd = await this.core.req?.sendAllow('/api/v4/users/' + this.core.user.id, rd.user, undefined, 'PUT');
            console.log('user edit results::', dd);
            if (dd && dd.message) {
                const message = dd.message ? dd.message : 'No se puedo actualizar el avatar...';
                this.core.winMsgError('Edit: ' + message);
            } else if (dd && dd.id) {
                this.core.updData(dd);
                return this.core.postCmd(req.command, this.core.user);
            } else this.core.winMsgError('Edit: Ocurri un error, intenta mas tarde...');
        }
        this.core.postCmd(req.command);
    }

    public async avatar(req: any) {
        const rd = req.content;
        const res = await this.core.winOpenImage();
        if (res && res.length) {

            const dd = await this.core.req?.sendAllow('/api/v4/users/' + this.core.user.id,
                { id: this.core.user.id }, { avatar: res[0].fsPath }, 'PUT',  this.core.user.isAdmin);
            if (dd && dd.message) {
                const message = dd.message.avatar
                    ? dd.message.avatar.toString()
                    : (dd.message ? dd.message : 'No se puedo actualizar el avatar...');
                this.core.winMsgError('Avatar: ' + message);
            } else if (dd && dd.id) {
                this.core.updData(dd);
                return this.core.postCmd(req.command, this.core.user);
            } else this.core.winMsgError('Avatar: Ocurri un error, intenta mas tarde...');
        }
        this.core.postCmd(req.command);
    }

    public async listByProject(req: any) {
        const rd = req.content;
        console.log('listByProject: ', rd.id);
        let data = await this.core.req?.getJson('/api/v4/projects/' + rd.id + '/users');
        data = getList(data, req.format);
        console.log('users data: ', data);
        this.core.postCmd(req.command, data);
    }

}