/* eslint-disable curly */
import { ExtensionContext, window } from "vscode";
import { SidebarProvider } from "./webviews/SidebarProvider";
import Core from "./services/Core";

/**
 * this method is called when your extension is activated
  * your extension is activated the very first time the command is executed
**/
export function activate(context: ExtensionContext) {
  const core = new Core('vscode-anemona-task', context);
  const ext = core.name;
  const sidebarProvider = new SidebarProvider(context, core);
  context.subscriptions.push(
    window.registerWebviewViewProvider(ext + '-sidebar', sidebarProvider),
    core.cmdReg(ext + '.refresh-en', async () => {
      await core.cmdExe("workbench.action.closeSidebar");
      await core.cmdExe("workbench.view.extension." + ext + "-sidebar");
    }),
    core.cmdReg(ext + '.refresh-es', async () => {
      await core.cmdExe("workbench.action.closeSidebar");
      await core.cmdExe("workbench.view.extension." + ext + "-sidebar");
    }),
    core.cmdReg(ext + '.login-en', () => sidebarProvider.page('page/login')),
    core.cmdReg(ext + '.lang-en', () => sidebarProvider.page('page/langEs')),
    //--
    core.cmdReg(ext + '.login-es', () => sidebarProvider.page('page/login')),
    core.cmdReg(ext + '.lang-es', () => sidebarProvider.page('page/langEn')),
    //------
    core.cmdReg(ext + '.logout-en', () => sidebarProvider.page('page/logout')),
    core.cmdReg(ext + '.home-en', () => sidebarProvider.page('page/dash')),
    core.cmdReg(ext + '.tasks-en', () => sidebarProvider.page('page/tasks')),
    //
    core.cmdReg(ext + '.logout-es', () => sidebarProvider.page('page/logout')),
    core.cmdReg(ext + '.home-es', () => sidebarProvider.page('page/dash')),
    core.cmdReg(ext + '.tasks-es', () => sidebarProvider.page('page/tasks')),
  );
  core.setMenu(1);
}

// this method is called when your extension is deactivated
export function deactivate() { }
