/* eslint-disable @typescript-eslint/naming-convention */
/* eslint-disable curly */
import Request from "./Request";
import { ExtensionContext, WebviewView, commands, window } from "vscode";


type FunCallPage = (data: any, opc: number) => {};

export default class Core {
    private sess: any;
    public user: any;
    public lang: string = 'en';
    public path: string;
    public cmdExe = commands.executeCommand;
    public cmdReg = commands.registerCommand;

    constructor(
        public name: string,
        public context: ExtensionContext,
        public view?: WebviewView,
        public req?: Request) {
        this.sess = {
            id: name + '.session',
            token: '',
            host: '',
            service: ''
        };
        this.path = '';
        this.user = null;
        this.isAuth();
    }

    public loadData(callback = (data: any) => { }): void {
        callback(this.user);
    }

    public getData(): any {
        return this.user ? this.user : this.getStsGlobal(this.sess.id);
    }

    public setData(host: string, token: string, service: string, data: any) {
        this.user = {
            allow: data.is_admin || service ? true : false,
            token: token,
            host: host,
            service: service,
            id: data.id,
            email: data.email,
            isAdmin: data.is_admin,
            username: data.username,
            name: data.name,
            lastActivity: data.last_activity_on,
            webUrl: data.web_url,
            avatarUrl: data.avatar_url
        };
        this.setStsGlobal(this.sess.id, this.user);
    }

    public get(id: string) {
        return this.getStsGlobal(this.sess.id + id);
    }

    public set(id: string, data: any) {
        this.setStsGlobal(this.sess.id + id, data);
    }

    public updData(data: any) {
        this.setData(this.sess.host, this.sess.token, this.sess.service, data);
    }

    public isAuth(): boolean {
        const data = this.getData();
        if (data) {
            this.user = data;
            this.sess.token = data.token;
            this.sess.host = data.host;
            this.sess.service = data.service;
            this.req = this.req ? this.req : new Request(this.sess.host, this.sess.token, this.sess.service);
            return true;
        }
        return false;
    }

    public async login(host: string, token: string, service: string = ''): Promise<boolean> {
        this.req = new Request(host, token, service);
        console.log('session - login init -');
        const data = await this.req.getJson('/api/v4/user');
        if (data && data.id) {
            console.log(data);
            this.setData(host, token, service, data);
            return this.isAuth();
        } else if (data && data.message) this.winMsgError(data.message);
        return false;
    }

    public async loginSession(data: any, callback: FunCallPage) {
        console.log('session send data', data)
        data && await this.login(data.host, data.token, data.service)
            ? this.loadData((user => {
                callback(user, 2);
            }))
            : callback(null, 1);
    }

    public dropSession(callback: FunCallPage) {
        this.delStsGlobal(this.sess.id);
        this.sess.token = '';
        this.sess.host = '';
        this.sess.service = '';
        this.user = null;
        callback(null, 1);
    }

    public validSession(callback: FunCallPage) {
        const auth = this.isAuth();
        callback({ success: auth, lang: this.lang }, auth ? 2 : 1);
    }

    public routeSession(cmd: [string], data: any, callback: any) {
        switch (cmd.shift()) {
            //case "lang": return this.loadLang(callback);
            case "valid": return this.validSession(callback);
            case "drop": return this.dropSession(callback);
            case "login": return this.loginSession(data, callback);
        }
    }

    public session(cmd: [string], req: any) {
        console.log('session - router:', cmd, req);
        const _cmd = 'session/' + cmd.join('/');
        this.postCmd('lang', this.lang);
        this.routeSession(cmd, req.content, (data: any, opc: number) => {
            console.log('session - _cmd ', _cmd);
            this.setMenu(opc);
            this.postCmd(_cmd, data || {});
        });
    }

    public initLang() {
        this.lang = this.getStsGlobal('lang') || 'en';
    }

    public loadLang(cmd: [string], req: any) {
        this.postCmd(req.command, this.lang);
    }

    public setLang(lang: string) {
        this.lang = lang;
        this.setStsGlobal('lang', lang);
        //this.postCmd('lang', this.lang);
    }

    public getStsGlobal(key: string): any {
        return this.context.globalState.get(key);
    }

    public setStsGlobal(key: string, data: any): void {
        this.context.globalState.update(key, data);
        //this.context.globalState.setKeysForSync([key]);
    }

    public delStsGlobal(key: string): void {
        this.context.globalState.update(key, undefined);
    }

    public setCtx(id: string, value: any): void {
        this.cmdExe('setContext', this.name + '.' + id, value);
    }

    public setMenu(option: number): void {
        if (this.lang === 'es') option = option === 1 ? 3 : (option === 2 ? 4 : 0);
        this.setCtx('idMenuOption', option);
    }

    public postCmd(cmd: string, data: any = {}): void {
        this.view?.webview.postMessage({ cmd: cmd, dat: data });
    };

    public setRequest(req: Request) {
        this.req = req;
    };

    public setView(view: WebviewView) {
        this.view = view;
    };

    public request(): Request | undefined {
        return this.req;
    };

    public winMsgInfo(message: string | undefined) {
        if (message) window.showInformationMessage(message);
    }
    public winMsgError(message: string | undefined) {
        if (message) window.showErrorMessage(message);
    }

    public async winOpenImage() {
        return await window.showOpenDialog({
            title: this.lang === 'es' ? 'Selecciona una imagen' : 'Select an image',
            canSelectFiles: true,
            canSelectFolders: false,
            canSelectMany: false,
            filters: { 'Images': ['png', 'jpg'] }
        });
    }
}